import Vue from 'vue'
import Router from 'vue-router'

import raceViewer from '@/components/RaceViewer'
Vue.use(Router)
export default new Router({
  mode: 'history',
  base: '/keyacc/',
  routes: [ {
      path: '/:raceName/',
      name: 'raceViewer',
      component: raceViewer,
      props: true
    }
  ]
})



